import React from 'react';
import { Container, Row, Col, Card, CardImg, CardText, CardBody,
    CardTitle, CardSubtitle, Button, FormGroup, Label, Input, Form } from 'reactstrap';
import { BrowserRouter as Router, Route, Link} from "react-router-dom";

import config from '../config';


export default class Auth extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            username: "",
            password: ""
        }
    }

    authUser = (e) => {

        e.preventDefault();

        fetch(config.url + '/api/auth', {
            method: 'POST',
            credentials: "same-origin",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({username: this.state.username, password: this.state.password})
        }).then((response) => {
            if(response.status !== 200) {
                alert("An error has occurred!");
                return;
            }
            return response.json();
        }).then((data) => {
            sessionStorage.setItem("token", data.api_token);
            window.location.href = "/profile";
        }).catch((e) => {
            alert("An error has occurred!");
        });
    };

    render() {
        return(
            <Container>
                <Row>
                    <Col md={{ size: 6, offset: 3 }}>
                        <Card>
                            <CardBody>
                                <CardTitle>Login</CardTitle>
                                <Form>
                                    <FormGroup>
                                        <Label for="username">Username</Label>
                                        <Input value={this.state.username} onChange={(e) => { this.setState({username: e.target.value}) }} type="text" name="username" id="username" placeholder="Username" />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="password">Password</Label>
                                        <Input value={this.state.password} onChange={(e) => { this.setState({password: e.target.value}) }} type="password" name="password" id="password" placeholder="Password" />
                                    </FormGroup>
                                    <Button onClick={this.authUser} color="primary" type="submit">Login</Button>
                                    <Link className="ml-3" to="/register">Register</Link>
                                </Form>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
            </Container>
        )
    }
}
