import React from 'react';
import { ListGroup, ListGroupItem  } from 'reactstrap';
import { BrowserRouter as Router, Route, Link} from "react-router-dom";


export default class Sidemenu extends React.Component {

    render() {
        return(
            <ListGroup>
                <ListGroupItem><Link to={"/profile"}>Profile</Link></ListGroupItem>
                <ListGroupItem><Link to={"/news"}>News</Link></ListGroupItem>
                <ListGroupItem><Link to={"/people"}>People</Link></ListGroupItem>
                <ListGroupItem><Link to={"/messages"}>Messgaes</Link></ListGroupItem>
                <ListGroupItem><Link to={"/profile/edit"}>Edit profile</Link></ListGroupItem>
                <ListGroupItem><Link to={"/auth"}>Exit</Link></ListGroupItem>
            </ListGroup>
        )
    }
}
