import React from 'react';
import { Container, Row, Col, Card, CardImg, CardText, CardBody,
    CardTitle, CardSubtitle, Button, FormGroup, Label, Input, Form, CustomInput, ListGroup, ListGroupItem  } from 'reactstrap';
import { BrowserRouter as Router, Route, Link} from "react-router-dom";
import Sidemenu from "./Sidemenu";
import config from "../config";




export default class EditProfile extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            name: "",
            surname: "",
            status: "",
            birthday: 0,
            city_id: 0,
            avatar: "",
            username: "",
            password: ""
        };

        this.fileInput = React.createRef();
    }

    editUser = (e) => {

        e.preventDefault();



        let formData  = new FormData();
        formData.append("name", this.state.name);
        formData.append("surname", this.state.surname);
        formData.append("status", this.state.status);
        formData.append("birthday", (new Date(this.state.birthday)).getTime() / 1000);
        formData.append("city_id", this.state.city_id);
        formData.append("avatar", this.fileInput.current.files[0]);
        formData.append("username", this.state.username);
        formData.append("password", this.state.password);

        fetch(config.url + '/api/profile', {
            method: 'PATCH',
            credentials: "same-origin",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'multipart/form-data',
            },
            body: formData
        }).then((response) => {
            if(response.status !== 200) {
                alert("An error has occurred!");
                return;
            }
            return response.json();
        }).then((data) => {
            window.location.href = "/profile";
        }).catch((e) => {
            alert("An error has occurred!");
        });
    };

    render() {
        return(
            <Container>
                <Row>
                    <Col md={{ size: 3, offset: 0}}>
                        <Sidemenu/>
                    </Col>
                    <Col md={{ size: 8, offset: 0 }}>
                        <Card>
                            <CardBody>
                                <CardTitle>Edit profile</CardTitle>
                                <Form>
                                    <FormGroup>
                                        <Label for="name">Name</Label>
                                        <Input onChange={(e) => { this.setState({name: e.target.value}) }} type="text" name="name" id="name" placeholder="Name" />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="surname">Surname</Label>
                                        <Input onChange={(e) => { this.setState({surname: e.target.value}) }} type="text" name="surname" id="surname" placeholder="Surname" />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="status">Status</Label>
                                        <Input onChange={(e) => { this.setState({status: e.target.value}) }} type="text" name="status" id="status" placeholder="Status" />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="birthday">Birthday</Label>
                                        <Input
                                            type="date"
                                            name="birthday"
                                            id="birthday"
                                            placeholder="Birthday"
                                            onChange={(e) => { this.setState({birthday: e.target.value}) }}
                                        />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="city">City</Label>
                                        <Input onChange={(e) => { this.setState({city_id: e.target.value}) }} type="select" name="city" id="city">
                                            <option value="1">1</option>
                                            <option value="2">2</option>
                                            <option value="3">3</option>
                                            <option value="4">4</option>
                                            <option value="5">5</option>
                                        </Input>
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="photo">Photo</Label><br/>
                                        <input ref={this.fileInput} type="file" id="photo" name="photo" />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="username">Username</Label>
                                        <Input onChange={(e) => { this.setState({username: e.target.value}) }} type="text" name="username" id="username" placeholder="Username" />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="password">Password</Label>
                                        <Input onChange={(e) => { this.setState({password: e.target.value}) }} type="password" name="password" id="password" placeholder="Password" />
                                    </FormGroup>
                                    <Button onClick={this.editUser} color="primary" type="submit">Save</Button>
                                </Form>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
            </Container>
        )
    }
}
