import React from 'react';
import { Container, Row, Col, Card, CardText, CardBody,
    CardTitle, Button, FormGroup, Label, Input, Form, CustomInput, } from 'reactstrap';
import Sidemenu from "./Sidemenu";

import config from '../config';


export default class Profile extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            name: "John Doe",
            status: "Волк слабее медведя, но в цирке не выступает",
            birthday: "",
            city: "",
            avatar: "",
            photos: [],
            posts: [],
            token: "",
            postText: "",
        };

        this.fileInput = React.createRef();
    }

    componentDidMount() {
        let token = sessionStorage.getItem("token");

        if(!token) {
            window.location.href = "/auth";
            return;
        }

        this.setState({
            token: token
        });

        fetch(config.url + '/api/profile', {
            method: 'GET',
            credentials: "same-origin",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + token
            }
        }).then((response) => {
            if(response.status !== 200) {
                alert("An error has occurred!");
                return;
            }
            return response.json();
        }).then((data) => {
           this.setState({
               name: data.name + " " + data.surname,
               birthday: data.birthday,
               city: data.city,
               photos: data.photos,
               posts: data.posts,
               status: data.status,
               avatar: data.avatar,
           });
        }).catch((e) => {
            alert("An error has occurred!");
        });
    }

    sendPost = (e) => {
        e.preventDefault();


        fetch(config.url + '/api/profile/posts', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.state.token
            },
            body: JSON.stringify({text: this.state.postText})
        }).then((response) => {
            if(response.status !== 200) {
                alert("An error has occurred!");
                return;
            }
            return response.json();
        }).then((data) => {
            console.log(data);
            this.setState({
                posts: data.posts
            });
            console.log(this.state.posts);
        }).catch((e) => {
            alert("An error has occurred!");
        });

    };

    sendPhoto = (e) => {
        e.preventDefault();



        let formData  = new FormData();

        formData.append("photo", this.fileInput.current.files[0]);

        fetch(config.url + '/api/profile/photos', {
            method: 'POST',
            credentials: "same-origin",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'multipart/form-data',
            },
            body: formData
        }).then((response) => {
            if(response.status !== 200) {
                alert("An error has occurred!");
                return;
            }
            return response.json();
        }).then((data) => {
            window.location.href = "/profile";
        }).catch((e) => {
            alert("An error has occurred!");
        });
    };

    deletePhoto = (id) => {

        fetch(config.url + '/api/profile/photos/'+id, {
            method: 'DELETE',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.state.token
            }
        }).then((response) => {
            if(response.status !== 200) {
                alert("An error has occurred!");
                return;
            }
            return response.json();
        }).then((data) => {
            console.log(data);
            this.setState({
                posts: data.posts
            });
            console.log(this.state.posts);
        }).catch((e) => {
            alert("An error has occurred!");
        });
    };

    editPost = (id) => {

        let text = prompt("New text for post");

        fetch(config.url + '/api/profile/posts/'+id, {
            method: 'PATCH',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.state.token
            },
            body: JSON.stringify({text: text})
        }).then((response) => {
            if(response.status !== 200) {
                alert("An error has occurred!");
                return;
            }
            return response.json();
        }).then((data) => {
            console.log(data);
            this.setState({
                posts: data.posts
            });
            console.log(this.state.posts);
        }).catch((e) => {
            alert("An error has occurred!");
        });

    };

    deletePost = (id) => {


        fetch(config.url + '/api/profile/posts/'+id, {
            method: 'DELETE',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.state.token
            }
        }).then((response) => {
            if(response.status !== 200) {
                alert("An error has occurred!");
                return;
            }
            return response.json();
        }).then((data) => {
            console.log(data);
            this.setState({
                posts: data.posts
            });
            console.log(this.state.posts);
        }).catch((e) => {
            alert("An error has occurred!");
        });

    };

    render() {
        return(
            <Container>
                <Row>
                    <Col md={{ size: 3, offset: 0}}>
                        <Sidemenu/>
                    </Col>
                    <Col md={{ size: 9, offset: 0 }}>
                        <Row className="mb-4">
                            <Col md={{ size: 5 }}>
                                <Card>
                                    <CardBody>
                                        <CardTitle>
                                            <img src={this.state.avatar} className="rounded float-left avatar" alt="Avatar"/>
                                        </CardTitle>
                                    </CardBody>
                                </Card>
                            </Col>
                            <Col md={{ size: 7 }}>
                                <h1>{this.state.name}</h1>
                                <span className="text-muted status">{this.state.status}</span><br/><br/>
                                <span className="text-muted userinfo"><strong>Birthday:</strong> {(new Date(this.state.birthday * 1000)).toLocaleDateString()}</span><br/>
                                <span className="text-muted userinfo"><strong>City:</strong> {this.state.city}</span>
                            </Col>
                        </Row>
                        <Row className="mb-4">
                            <Col md={{ size: 5 }}>
                                <Card className="mb-3">
                                    <CardBody>
                                        <Form>
                                            <FormGroup>
                                                <Label for="photo">Photo</Label>
                                                <input ref={this.fileInput} type="file" id="photo" name="photo" />
                                            </FormGroup>
                                            <Button onClick={this.sendPhoto} color="primary" type="submit" className="mt-3">Upload</Button>
                                        </Form>
                                    </CardBody>
                                </Card>
                                {
                                    this.state.photos.map((photo) => {
                                        return <div><img key={photo.link} src={photo.link} alt="Photo" className="img-thumbnail"/>
                                                <Button onClick={() => {this.deletePost(photo.id)}} color="danger" className="mb-2">Delete</Button>
                                        </div>
                                    })
                                }

                            </Col>
                            <Col md={{ size: 7 }}>
                                <Card className="mb-3">
                                    <CardBody>
                                        <Form>
                                            <Input value={this.state.postText} onChange={(e) => { this.setState({ postText: e.target.value }) }} type="textarea" name="postText" id="postText" />
                                            <Button onClick={this.sendPost} color="primary" type="submit" className="mt-3">Public</Button>
                                        </Form>
                                    </CardBody>
                                </Card>
                                {
                                    this.state.posts.map((post) => {
                                        return <Card className="mb-2">
                                            <CardBody>
                                                <CardTitle>
                                                    { (new Date(post.date * 1000)).toLocaleDateString() }
                                                </CardTitle>
                                                <hr/>
                                                <CardText>{ post.text }</CardText>
                                                <Button onClick={() => {this.editPost(post.id)}} color="warning" className="mr-2">Edit</Button>
                                                <Button onClick={() => {this.deletePost(post.id)}} color="danger">Delete</Button>
                                            </CardBody>
                                        </Card>
                                    })
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Container>
        )
    }
}
